<template>
  <div id="matterD">
    <h2 class="title">今日各{{ matter }}事项详情</h2>
    <div class="detailsWarp">
      <div class="details" v-for="(item, index) in detailsData" :key="index">
        <div class="itemTitle">{{ item.index }}. 流水号：{{ item.water_num }}</div>
        <el-timeline>
          <el-timeline-item
                  v-for="(it, ind) in item.processinfo" :key="ind"
                  :color="it.result === 'Y' ? '#0bbd87' : '#ff6a1e' "
                  :timestamp="it.starts"
                  placement="top">
            <el-card>
              <h4 style="margin-bottom: 10px;height:25px;line-height:25px;vertical-align:middle;">
                <span style="display: inline-block;vertical-align:middle;"><svg-icon icon-class="processUser" style="width: 25px;height: 25px;" /></span> <span style="display: inline-block;vertical-align:middle;">{{ it.current_process }}</span>
              </h4>
              <p>{{ it.user }} 提交于 {{ it.ends }}</p>
              <p style="line-height: 1.2em;">备注：<span v-text=" it.discription || '无' "></span></p>
            </el-card>
          </el-timeline-item>
          <el-timeline-item :timestamp="nextProcess" placement="top">
            <el-card>
              <h4 style="height:25px;line-height:25px;vertical-align:middle;">
                <span style="display: inline-block;vertical-align:middle;"><svg-icon icon-class="nextProcess" style="width: 25px;height: 25px;" /></span> <span style="display: inline-block;vertical-align:middle;">{{ item.next_process }}</span>
              </h4>
            </el-card>
          </el-timeline-item>
        </el-timeline>
      </div>
    </div>
  </div>
</template>

<script>
import store from '@/store/index'
import supportFun from '@/supportFunction/index'
export default {
  name: 'Sign',
  store,
  data () {
    return {
      detailsData: [],
      nextProcess: '下一步'
    }
  },
  watch: {
  },
  computed: {
    matter: function () {
      return sessionStorage.detailsMatter || ''
    }
  },
  created: function () {
    this.matterDetailsFn({item: this.matter})
  },
  methods: {
    async matterDetailsFn (params) {
      try {
        let res = await this.$api.unsplash.itemHistory(params)
        console.log(res)
        if (res.errno === '0') {
          this.detailsData = supportFun.addSequence(res.data)
        } else {
          this.detailsData = []
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.detailsData = []
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    notifyFn (title, msg) {
      this.$notify({
        title: title,
        message: msg,
        type: 'warning',
        dangerouslyUseHTMLString: true,
        duration: 5000,
        position: 'top-right'
      })
    }
  }
}
</script>
<style lang="scss">
  #matterD{
    width: 100vw;
    min-height: 100vh;
    position: absolute;
    top: 0;left: 0;
    background: #2c3e50;
    color: #ffffff;
    .title{
      margin: 20px 0 20px 0;
    }
    .detailsWarp{
      width: 600px;
      margin: 0 auto;
      text-align: left;
      .details{
        .itemTitle{
          margin-bottom: 10px;
        }
      }
    }
  }
</style>



